.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.CountryPanel {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;

  h1 {
    margin: 0;
    padding: 0.5em;
  }

  .PedestalContainer {
    height: 50%;
    width: 70%;
    margin-top: 2em;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-direction: row;
    justify-content: space-around;

    div {
      display: flex;
      justify-content: space-between;
      align-items: center;
      min-height: 20%;
      max-height: 100%;
    }
  }
}